.map__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.map__wrapper p {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 31px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  max-width: 555px;
  margin-top: 30px;
  margin-bottom: 41px;
}

.map__wrapper h1 {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
}

.map {
  margin: 0 auto;
  width: 957px;
  height: 530px;
}

.map__footer {
  margin-top: 40px;
  margin-bottom: 100px;
}

.map__link {
  position: relative;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.5);

  &:hover {
    color: #000000;
  }

  &--mail {
    margin-right: 127px;
  }

  &--mail::before {
    content: "";
    position: absolute;
    width: 26px;
    height: 20px;
    left: -40px;
    top: 3px;
    background: url("../../static/images/content/mail.svg");
  }

  &--phone::before {
    content: "";
    position: absolute;
    width: 22px;
    height: 24px;
    left: -40px;
    top: 1px;
    background: url("../../static/images/content/phone.svg");
  }
}

@media (max-width: $tablet-width) {
  .map {
    width: 698px;
    height: 545px;
  }

  .map__wrapper p {
    max-width: none;
  }

  .map__wrapper h1 {
    font-size: 24px;
    line-height: 30px;
  }
}

@media (max-width: $mobile-width) {
  .map__wrapper p {
    max-width: 288px;
  }

  .map__wrapper h1 {
    font-size: 18px;
  }

  .map {
    width: 320px;
    height: 250px;
  }

  .map__link {
    &--mail {
      margin: 0;
      margin-bottom: 22px;
    }
  }

  .map__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 20px;
  }
}
