.video__wrapper {
  background-color: #000000;
}

.video {
  position: relative;
  margin-bottom: 150px;
}

.video__block h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 20px;
}

.video__block p {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  opacity: 0.7;
  color: #ffffff;
  max-width: 340px;
  text-align: center;
}


.video__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 120px;
}


.video__content-block {
  position: relative;
  top: 100px;

  &::before {
    content: "";
    position: absolute;
    width: 65px;
    height: 65px;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    background-color: #0ad9c6;
  }

  &::after {
    content: "";
    position: absolute;
    width: 13px;
    height: 18.2px;
    top: 54.5%;
    left: 53%;
    border: 9px solid transparent;
    border-left: 14px solid #000000;
  }

  &:hover::before {
    background-color: gray;
  }

  &:hover::after {
    border: 9px solid transparent;
    border-left: 14px solid #ffffff;
  }
}

@media (max-width: $tablet-width) {
  .video__block h1 {
    font-size: 24px;
    line-height: 30px;
  }

  .video__block p {
    font-size: 16px;
    line-height: 28px;
    max-width: 430px;
  }

  .video__content-block img {
    width: 503px;
    height: 282px;
  }

  .video__content-block {
    margin-bottom: 70px;
    &::before {
      left: 44%;
    }

    &::after {
      top: 58.5%;
      left: 49.5%;
    }
  }
}

@media (max-width: $mobile-width) {
  .video__content-block img {
    width: 288px;
    height: 161.66px;
  }

  .video__content-block {
    &::before {
      left: 37%;
      top: 33%;
    }

    &::after {
      top: 47.5%;
      left: 46.5%;
    }
  }
}
