.video__content-block {
  position: relative;
  top: 100px; }
  .video__content-block::before {
    content: "";
    position: absolute;
    width: 65px;
    height: 65px;
    top: 50%;
    left: 50%;
    border-radius: 50%;
    background-color: #0ad9c6; }
  .video__content-block::after {
    content: "";
    position: absolute;
    width: 13px;
    height: 18.2px;
    top: 54.5%;
    left: 53%;
    border: 9px solid transparent;
    border-left: 14px solid #000000; }
  .video__content-block:hover::before {
    background-color: gray; }
  .video__content-block:hover::after {
    border: 9px solid transparent;
    border-left: 14px solid #ffffff; }

@media (max-width: 1023px) {
  .video__block h1 {
    font-size: 24px;
    line-height: 30px; }
  .video__block p {
    font-size: 16px;
    line-height: 28px;
    max-width: 430px; }
  .video__content-block img {
    width: 503px;
    height: 282px; }
  .video__content-block {
    margin-bottom: 70px; }
    .video__content-block::before {
      left: 44%; }
    .video__content-block::after {
      top: 58.5%;
      left: 49.5%; } }

@media (max-width: 767px) {
  .video__content-block img {
    width: 288px;
    height: 161.66px; }
  .video__content-block::before {
    left: 37%;
    top: 33%; }
  .video__content-block::after {
    top: 47.5%;
    left: 46.5%; } }

.map__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto; }

.map__wrapper p {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 31px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  max-width: 555px;
  margin-top: 30px;
  margin-bottom: 41px; }

.map__wrapper h1 {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px; }

.map {
  margin: 0 auto;
  width: 957px;
  height: 530px; }

.map__footer {
  margin-top: 40px;
  margin-bottom: 100px; }

.map__link {
  position: relative;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.5); }
  .map__link:hover {
    color: #000000; }
  .map__link--mail {
    margin-right: 127px; }
  .map__link--mail::before {
    content: "";
    position: absolute;
    width: 26px;
    height: 20px;
    left: -40px;
    top: 3px;
    background: url("../../static/images/content/mail.svg"); }
  .map__link--phone::before {
    content: "";
    position: absolute;
    width: 22px;
    height: 24px;
    left: -40px;
    top: 1px;
    background: url("../../static/images/content/phone.svg"); }

@media (max-width: 1023px) {
  .map {
    width: 698px;
    height: 545px; }
  .map__wrapper p {
    max-width: none; }
  .map__wrapper h1 {
    font-size: 24px;
    line-height: 30px; } }

@media (max-width: 767px) {
  .map__wrapper p {
    max-width: 288px; }
  .map__wrapper h1 {
    font-size: 18px; }
  .map {
    width: 320px;
    height: 250px; }
  .map__link--mail {
    margin: 0;
    margin-bottom: 22px; }
  .map__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 20px; } }
