.advantages {
  width: 100%;
  max-width: 1120px;
  margin: 0 auto;
  margin-bottom: 130px;
}

.advantages h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: #1f1f1f;
  text-align: center;
  margin-bottom: 50px;
}

.advantages__list {
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 38px;

  &:last-child {
    margin-right: 0;
  }
}

.advantages__item h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  text-align: center;
  color: #0ad9c6;
  margin-bottom: 15px;
}

.advantages__item p {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  color: #000000;
}

@media (max-width: $tablet-width) {
  .advantages__list {
    flex-wrap: wrap;
    max-width: 536px;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
  }

  .advantages h1 {
    font-size: 24px;
    line-height: 41px;
  }

  .advantages__item p {
    max-width: 238px;
  }

  .advantages__item {
    margin: 0;
  }

  .advantages {
    margin-top: 300px;
  }
}

@media (max-width: $mobile-width) {
  .advantages__item p {
    font-size: 18px;
    line-height: 34px;
  }

  .advantages__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .advantages {
    margin-top: 170px;
  }
}

@media (max-width: 480px) {
  .advantages__list {
    flex-wrap: nowrap;
    flex-direction: column;
    max-width: 290px;
  }
}
