.types__block {
  width: 100%;
  max-width: 1126px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.types h1 {
  text-align: center;
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  margin-bottom: 60px;
}

.types__block-item {
  position: relative;

  &--center {
    margin-top: 30px;
  }

  &--center::before {
    content: "";
    position: absolute;
    width: 47px;
    height: 47px;
    top: -18px;
    left: -17px;
    background: url("../../static/images/content/square.svg") no-repeat;
  }

  &--center::after {
    content: "";
    position: absolute;
    width: 47px;
    height: 47px;
    right: -18px;
    bottom: 40px;
    background: url("../../static/images/content/square.svg") no-repeat;
  }

  &--last::before {
    content: "";
    position: absolute;
    width: 95px;
    height: 95px;
    top: -44px;
    right: -37px;
    background: url("../../static/images/content/circle-min.svg") no-repeat;
  }

  &--first::before {
    content: "";
    position: absolute;
    width: 180px;
    height: 180px;
    top: 25%;
    left: -75px;
    background: url("../../static/images/content/circle.svg") no-repeat;
    z-index: 4;
  }

  &--first img {
    position: relative;
    z-index: 10;
  }
}

.types__block-item p {
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  color: #000000;
  text-align: center;
  margin-top: 20px;
}


.types {
  background: #f6f7f9;
  padding-top: 130px;
  padding-bottom: 147px;
}

@media (max-width: 1284px) {
  .types__block {
    max-width: 1000px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .types__block-item {
    max-width: 342px;

    &--center {
      margin: 0;
      margin-left: 150px;
    }

    &--center img {
      height: 512px;
    }

    &--last {
      margin-top: 70px;
    }
  }
}

@media (max-width: $tablet-width) {
  .types__block-item img {
    width: 270px;
    height: 300px;
  }

  .types__block {
    flex-wrap: wrap;
    justify-content: center;
  }

  .types__block-item {
    &--center {
      margin: 0;
      margin-left: 30px;
      margin-right: 30px;
    }

    &--center::after {
      display: none;
    }

    &--center::before {
      display: none;
    }

    &--first::before {
      content: "";
      position: absolute;
      width: 127px;
      height: 127px;
      top: 57%;
      left: -38px;
      background: url("../../static/images/content/circle-tab.svg") no-repeat;
      z-index: 4;
    }

    &--last {
      margin: 0;
    }
  }

  .types {
    padding-bottom: 70px;
  }
}


@media (max-width: 950px) {
  .types__block {
    max-width: 600px;
  }

  .types__block-item {
    &--center {
      margin: 0;
      margin-left: 50px;
    }

    &--last {
      margin-top: 50px;
    }

    &--last::before {
      content: "";
      position: absolute;
      width: 68px;
      height: 68px;
      top: -24px;
      right: -22px;
      background: url("../../static/images/content/circle-min-tab.svg") no-repeat;
    }
  }
}

@media (max-width: $mobile-width) {
  .types__block-item {
    &--center {
      margin-left: 20px;
    }
  }
}


@media (max-width: 650px) {
  .types__block {
    max-width: 270px;
    flex-wrap: nowrap;
    flex-direction: column;
  }

  .types__block-item {
    &--center {
      margin: 0;
      margin-top: 50px;
    }
  }
}
