.header-content__container {
  display: flex;
}

.header-content__text-block h1 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px;
  color: #ffffff;
}

.header-content__text-block p {
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  mix-blend-mode: normal;
  opacity: 0.8;
}

.header-content__form-block h2 {
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  color: #ffffff;
  margin-bottom: 20px;
}

.header-content {
  margin: 0 auto;
  margin-top: 260px;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  width: 100%;
}

.header-content__form-block {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-content__form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 327px;
}

.header-content__input {
  margin-bottom: 20px;
  border-radius: 4px;
  padding: 10px;

  &::placeholder {
    font-family: $fontfamali;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.5;
  }
}

.header-content__btn-sub {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: bold;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  padding: 15px;
  background: #0ad9c6;
  border-radius: 6px;
}

@media (max-width: $tablet-width) {
  .header-content__text-block img {
    display: none;
  }

  .header-content__form-block {
    margin-left: 225px;
    margin-right: 225px;
  }

  .header-content__form {
    max-width: none;
  }

  .header-content__text-block {
    width: 100%;
    max-width: 425px;
    margin: 0 auto;
    margin-bottom: 130px;
  }

  .header-content__text-block h1 {
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    margin: 0 auto;
  }

  .header-content__text-block p {
    font-size: 18px;
    line-height: 32px;
    font-weight: 300;
    text-align: center;
    margin: 0 auto;
  }

  .header-content__container {
    flex-direction: column;
  }

  .header-content__input {
    border: 1px solid #c9ccd4;
  }

  .header-content__form-block h2 {
    width: 100%;
    //max-width: 320px;
    color: #000000;
  }

  .header-content {
    margin-top: 203px;
  }

  .header-content__logo-block {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .header-content {
    margin-top: 141px;
  }

  .header-content__form-block {
    margin-left: 16px;
    margin-right: 16px;
  }

  .header-content__text-block {
    margin-left: 16px;
    margin-right: 16px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 160px;
  }

  .header-content__container {
    align-items: center;
  }

  .header-content__text-block p {
    margin-right: 16px;
    margin-left: 16px;
  }
}
